*{
  font-family: sans-serif;
}
body{
  padding: 4rem;
}
input{
  width: 100%;
  font-size: 1.2rem;
  outline: none;
  color: #333;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: .1rem solid grey;
  border-radius: 1rem;
  margin-bottom: 1rem;
  margin-right: 1rem;
  overflow: hidden;
  word-wrap: break-word;
}

.card p{
  padding: .3rem;
  font-size: 1rem;
  text-align: center;
}

.card img{
  max-height: 12rem;
  width: 100%;
  object-fit: cover;
}